.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__wrapper-img {
  text-align: center;
  margin-bottom: 36px;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rewiews-wrap {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__count-rewiews {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
