  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px 70px 11px 69px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: block;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
